@font-face {
  font-family: 'Roboto';
  font-style: normal;
	font-weight: 700;
  src: url(./fonts/Roboto/Roboto-Bold.ttf);
}

@font-face {
  font-family: 'Roboto';
  src: url(./fonts/Roboto/Roboto-Regular.ttf);
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: url(./fonts/Roboto/Roboto-Thin.ttf);
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: url(./fonts/Roboto/Roboto-Medium.ttf);
  font-weight: 500;
  font-style: normal;
}

