$white: #ffffff;
$white-w1: #eeeeee;
$btn-main: #ffc940;
$black: #000000;
$blue: #0d46a0;
$blue-b1: #002071;
$black-b1: #353535;

html {
  min-height: 100%;
}

h2 {
  font-size: 42px;
  color: $blue;
  margin: 0;
  font-weight: 600;
}

body {
  font-family: Roboto, Arial, Tahoma, sans-serif;
  font-size: 16px;
  padding: 0;
  margin: 0 auto;
  font-weight: 400;
}

.button {
  display: flex;
  justify-content: center;
  
  &-item {
    color: $black;
    background-color: $btn-main;
    border: none;
    border-radius: 30px;
    font-weight: bold;

    &-md {
      height: 60px;
      font-size: 16px;
      padding-left: 60px;
      padding-right: 60px;
    }

    &-sm {
      height: 45px;
      font-size: 14px;
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: $photo-1;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &-container {
    padding-top: 200px;
    max-width: 560px;

    h1, h3 {
      text-align: center;
      color: $white;
    }
    .title {
      margin: 0 0 40px 0;
      font-size: 48px;
      font-weight: 500;
    }
    .sub-title {
      margin: 0 0 110px 0;
      font-size: 24px;
      font-weight: 500;
    }
  }
}

.container {
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.col {
  width: 100%;
  margin-left: 20px;
  margin-right: 20px;

  &-6 {
    max-width: 560px;
  }

  &-12 {
    max-width: 1160px;
  }

  &-right-cl {
    margin-right: 0;
  }

  &-left-cl {
    margin-left: 0;
  }
}

.img {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

.pricing {
  &-title {
    text-align: center;
    margin-bottom: 105px;
  }

  &-wrapper {
    display: flex;
  }

  .group {
    width: 100%;
    height: 240px;

    &-1 {
      background-image: $photo-2;
    }
    &-2 {
      background-image: $photo-3;
    }
    margin-bottom: 27px;
  }

  &-description {
    .title {
      font-size: 20px;
      color: $blue;
      margin: 0;
      padding: 0;
      font-weight: 600;
    }
    .text {
      font-size: 16px;
      color: $blue-b1;
      margin: 0;
      padding: 0;
      font-weight: 300;
      line-height: 1.55;
    }
    .sub-title {
      margin-top: 12px;
      margin-bottom: 17px;
    }
  }

  .button {
    margin-top: 25px;
    justify-content: flex-start;
  }
}

.divider {
  height: 120px;
}

.paradise {
  &-title {
    text-align: center;
    margin-bottom: 90px;
  }

  &-item {
    display: flex;
    align-items: center;

    &-img {
      height: 420px;

      &-1 {
        background-image: $boat;
      }

      &-2 {
        background-image: $photo-4;
      }

      &-3 {
        background-image: $photo-5;
      }

      &-4 {
        background-image: $photo-6;
      }
    }
    &-text {
      max-width: 500px;
      p {
        margin: 0;
        padding: 0;
        font-size: 20px;
        font-weight: 300;
        line-height: 1.55;
        color: $blue-b1;
      }
    }
  }
}

.tour-includes {
  padding-top: 60px;
  padding-bottom: 60px;

  .tab {

    &-folder {
      background-color: $white;
      border: 0px solid $white-w1;
      box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 30%);
      max-width: 960px;
      margin: 0 auto;
    }
    &-header {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 67px;

      > div {
        color: $black;
        font-size: 20px;
        font-weight: 700;
        text-transform: uppercase;
        padding: 18px 20px;
        line-height: 1.35;
        cursor: pointer;
      }

      .active {
        color: $blue !important;
        font-weight: 700 !important;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          z-index: 1;
          width: 100%;
          height: 3px;
          background-color: $blue;
        }
      }
    }

    &-body {
      display: none;

      &-active {
        display: flex;
      }
      justify-content: center;
      padding: 40px;
    }

    &-image {
      width: 330px;
      height: 240px;

      &-1 {
        background-image: $photo-7;
      }

      &-2 {
        background-image: $photo-8;
      }
    }
  }

  .left-data {
    padding-right: 20px;
  }

  .right-data {
    padding-left: 20px;

    ul {
      padding: 0;
      margin: 0;

      color: $blue-b1;
      font-size: 20px;

      li {
        line-height: 1.55;
        font-weight: 300;
      }
    }
  }
}

.tour {
  &-plan {
    display: flex;
  }

  .info {
    padding-left: 20px;
  }
  
  &-title {
    padding-bottom: 28px;
    margin: 0;
    font-size: 28px;
    line-height: 1.17;
    color: $blue;
    font-weight: 600;
    text-align: center;
  }

  &-list {
    color: $blue-b1;
    font-size: 20px;
    padding: 0 0 0 16px;
    margin: 0;

    li {
      line-height: 1.55;
      font-weight: 300;
    }
  }

  &-image {
    width: 560px;
    height: 310px;
    background-image: $photo-8;
  }
}

.boat {
  display: flex;

  &-image {
    width: 560px;
    height: 315px;

    .image {
      height: 100%;
      background-image: $photo-10;
      background-position-y: 75%;
    }
  }

  .info {
    &-title {
      color: $blue;
      font-size: 18px;
      line-height: 1.50;
      font-weight: bold;
    }

    &-description {
      margin: 0;
      padding: 0;
      font-size: 18px;
      color: $blue-b1;
      font-weight: 300;
      line-height: 1.50;
    }
  }

  .link-container {
    margin-top: 60px;

    :first-child {
      margin-bottom: 10px;
    }

    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.program {
  .day-section:last-child {
    padding-bottom: 0;
  }

  &-title {
    font-size: 30px;
    text-align: center;
    color: $blue;
    line-height: 1.23;
    font-weight: 600;
    margin: 0 0 50px 0;
  }

  .day {
    &-section {
      padding-bottom: 60px;
      display: flex;
      width: 100%;

      &-min-height {
        min-height: 560px;
      }
    }

    &-info {
      .info {
        max-width: 460px;
        margin: 0 0 0 auto;
      }
    }

    &-header {
      font-size: 24px;
      color: $blue;
      font-weight: 24px;
      text-align: center;
    }

    &-description {
      font-size: 18px;
      line-height: 1.55;
      color: $blue-b1;
      font-weight: 300;
    }

    &-collage {
      .collage-image-full {
        height: 100%;
        max-width: 460px;
      }
      .collage-image-small {
        max-width: 460px;
        height: 100%;
        max-height: 400px;
      }

      .c-9 {
        background-image: $photo-19;
      }

      .c-10 {
        background-image: $photo-20;
      }
      .c-11 {
        background-image: $photo-22;
      }
      .c-12 {
        background-image: $photo-22;
      }
    }

    &-attention {
      > div {
        display: flex;
        align-items: center;
        justify-content: center;

        :first-child {
          margin-right: 10px;
          font-size: 35px;
          color: $blue;
          font-weight: 600;
        }
      }

      font-size: 18px;
      line-height: 1.55;
      color: $blue-b1;
      font-weight: 300;

      .icon {
        font-size: 25px;
      }
    }
  }
}

.collage {
  position: relative;
  display: flex;
  height: 100%;
  max-height: 500px;

  &-item {
    height: 100%;

    .last {
      width: 100%;
      height: 100%;
      background-image: $temp-bg;
    }
  }

  .c-1 {
    background-image: $photo-12;
  }

  .c-2 {
    background-image: $photo-12;
  }

  .c-3 {
    background-image: $photo-13;
  }

  .c-4 {
    background-image: $photo-14;
  }

  .c-5 {
    background-image: $photo-15;
  }

  .c-6 {
    background-image: $photo-16;
    background-position-y: 25%;
  }

  .c-7 {
    background-image: $photo-18;
  }

  .c-8 {
    background-image: $photo-18;
  }

  .c-13 {
    background-image: $photo-24;
  }
  .c-14 {
    background-image: $photo-24;
  }
  .c-15 {
    background-image: $photo-25;
    background-position-y: 30%;
  }

  .first {
    width: 100%;
    min-width: 315px;
    padding-right: 10px;

    > div {
      height: 80%;
    }
  }

  .second {
    width: 100%;

    > div {
      height: 50%;
      margin-top: 24%;
    }
  }

  .third {
    width: 324px;
    height: 200px;
    position: absolute;
    left: 25%;
    bottom: -20px;
  }

  .item {
    box-shadow: 0px 0px 15px 0px rgb(0 0 0 / 20%);
  }

  &-two-items {
    .first {
      max-width: 450px;
    }
    .last {
      right: 0;
      left: unset;
    }
    .third {
      left: 30%;
    }
  }
}

.accompaniment {
  .title {
    font-size: 42px;
    color: $blue;
    line-height: 1.23;
    font-weight: 600;
    text-align: center;
    margin-bottom: 90px;
    margin-top: 0;
  }

  &-section {
    display: flex;
    align-items: center;

    &-separator {
      height: 120px;

      &-small {
        height: 60px;
      }
    }
  }

  .section {
    &-image {
      .image {
        background-image: $temp-bg;
        height: 100%;
        min-height: 365px;
        
        &-big {
          min-height: 500px;
        }
      }

      .c-1 {
        background-image: $photo-26;
      }

      .c-2 {
        background-image: $photo-27;
        background-position-y: 30%;
      }

      .c-3 {
        background-image: $photo-30;
      }

      .c-4 {
        background-image: $photo-30;
      }

      .c-5 {
        background-image: $photo-30;
      }

      .c-6 {
        background-image: $photo-30;
      }
    }

    &-info {
      &-text {
        font-size: 18px;
        color: $blue;
        line-height: 1.55;
        font-weight: 300;
        margin: 0;
        padding: 0;

        .important-info {
          font-weight: bold;
        }
      }

      &-title {
        padding: 0px;
        margin: 0px;
        padding-bottom: 28px;
        font-size: 24px;
        line-height: 1.17;
        font-weight: 600;
        color: $blue;
        text-align: center;
      }

      &-list {
        padding-left: 20px;
        color: $blue;
        margin-top: 5px;
        margin-bottom: 0;

        li {
          line-height: 1.55;
          font-size: 18px;
          font-weight: 300;
        }
      }
    }
  }
}

.gallery {
  &-title {
    color: $blue;
    font-size: 32px;
    margin-top: 0;
    margin-bottom: 40px;
    text-align: center;
  }

  .my-slider {
    .slide img {
      max-height: 550px;
      max-width: 860px;
      width: 100%;
    }
  }
}

.owl-dots {
  margin-top: 10px;
  text-align: center;

  .owl-dot {
    span {
      width: 10px;
      height: 10px;
      margin: 5px 7px;
      background-image: #D6D6D6;
      display: block;
      transition: opacity .2s ease;
      border-radius: 30px;
    }
  }
}

.owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span {
  background-image: #869791;
}

.faq {

  &-title {
    font-size: 42px;
    color: $blue;
    font-weight: 600;
    padding-bottom: 15px;
    text-align: center;
    padding: 0;
  }
  &-body {
    max-width: 760px;
    width: 100%;
    margin: 0 auto;

    border-top: 1px solid $white-w1;
    :last-child {
      border-bottom: none;
    }
  }

  &-item {
    transition: all 1s;
    border-bottom: 1px solid $white-w1;
    
    .item {
      &-container {
        position: relative;
        display: flex;
        align-items: center;
        cursor: pointer;

        .toggle {
          transition: all .2s;
          height: 40px;
          width: 40px;
          border-radius: 50px;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;

          &:after, &:before {
            content: '';
            width: 25px;
            height: 1px;
            display: block;
            background-color: $black-b1;
            position: absolute;
          }

          &:after {
            transform: rotate(90deg);
          }
        }
  
        &:hover .toggle {
          background-color: $white-w1;
        }
      }

      &-title {
        font-size: 24px;
        color: $blue;
        line-height: 1.35;
        font-weight: 600;
        margin: 0;
        width: 100%;
        padding: 27px 50px 27px 0;
        text-align: left;
        background-color: transparent;
        border: none;
        box-sizing: border-box;
        cursor: pointer;
      }

      &-description {
        color: $blue-b1;
        font-size: 18px;
        line-height: 1.55;
        font-weight: 300;
        margin-top: 0;
      }

      &-content {
        transition: all 1s;
        overflow: hidden;
        max-height: 0;
      }
    }
  }

  .active {
    transition: all 1s;

    .item-content {
      transition: all 1s;
      max-height: 500px;
    }

    .toggle {
      transition: all .2s;
      transform: rotate(45deg);
      background-color: $white-w1;
    }
  }
}

.tour-approve {
  height: 100vh;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
  background-image: $photo-4;

  .approve-title {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    color: $white;
    font-size: 32px;
    line-height: 1.23;
    font-weight: 600;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;

  &-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 460px;
    width: 100%;
     
    .title {
      font-size: 24px;
      color: $blue-b1;
      line-height: 1.35;
      font-weight: 600;
      margin: 0;
      padding: 0;
    }

    .divider {
      width: 54px;
      max-width: 100%;
      height: 2px;
      margin: 20px auto;
      background-color: #0d46a0;
      opacity: 0.60;
    }

    .button {
      width: 100%;
      height: 51px;
      
      > button {
        cursor: pointer;
        color: $white;
        width: 100%;
        background-color: #27d061;
        font-weight: 300;
        font-size: 16px;
      }
    }
  }
}